import { Stack, TextField } from '@mui/material';
import React, { FC, useEffect, useRef, useState } from 'react';
import { ADD_MEMORY_CONTENT_PLACEHOLDER } from './constants';
import { TAddMemoryContentProps } from './types';

export const AddMemoryContent: FC<TAddMemoryContentProps> = ({
    preventClose
}) => {
    const [value, setValue] = useState('');

    const inputRef = useRef(null);

    useEffect(() => {
        setTimeout(() => {
            const input = inputRef?.current.querySelector('textarea');
            if (!input) return;
            input.focus();
        }, 0);
    }, []);

    useEffect(() => {
        if (!value || !preventClose) return;
        preventClose();
    }, [value]);

    return <Stack style={containerStyle}>
        <TextField
            ref={inputRef}
            multiline
            autoFocus
            value={value}
            onChange={e => setValue(e.currentTarget.value)}
            placeholder={ADD_MEMORY_CONTENT_PLACEHOLDER}
            variant={'standard'}
            sx={inputStyle}
        />
    </Stack>;
};

const containerStyle = {
    height: '100%'
};

const inputStyle = {
    flexGrow: 1,
    '.MuiInputBase-root': {
        height: 1,
        '&:after, &:before': {
            display: 'none'
        }
    },
    '.MuiInputBase-input': {
        height: '100% !important'
    }
};