import { Popover, Stack } from '@mui/material';
import React, { useRef, useState } from 'react';
import { theme } from '../../style/theme';
import { AddMemoryButton } from '../addMemoryButton/addMemoryButton';
import { AddMemoryContent } from '../addMemoryContent/addMemoryContent';

export const AddMemory = () => {
    const [open, setOpen] = useState(false);
    const [keepOpen, setKeepOpen] = useState(null);

    const ref = useRef(null);
    const onToggle = () => {
        setOpen(!open);
    };

    const onClose = () => {
        onToggle();
        setKeepOpen(false);
    };

    const onClick = () => {
        if (open) {
            setKeepOpen(false);
        } else {
            setKeepOpen(true);
        }
        onToggle();
    };

    const onMouseEnter = () => {
        if (keepOpen || open) return;
        onToggle();
    };

    const onMouseLeave = () => {
        if (keepOpen || !open) return;
        onToggle();
    };

    return <Stack sx={containerStyle} ref={ref}>
        <AddMemoryButton
            onClick={onClick}
            onMouseLeave={onMouseLeave}
            onMouseEnter={onMouseEnter}
            open={open}
        />
        <Popover
            sx={{
                pointerEvents: keepOpen ? 'all' : 'none',
                zIndex: theme.zIndex.mobileStepper
            }}
            open={open}
            anchorEl={ref?.current}
            anchorOrigin={{
                vertical: 'top',
                horizontal: 'left',
            }}
            transformOrigin={{
                vertical: 'bottom',
                horizontal: 'right',
            }}
            onClose={onClose}
            PaperProps={{ sx: paperStyle, onMouseEnter, onMouseLeave }}
        >
            <AddMemoryContent preventClose={() => setKeepOpen(true)}/>
        </Popover>
    </Stack>;
};

const containerStyle = {
    position: 'fixed',
    bottom: theme.spacing(3),
    right: theme.spacing(3),
    zIndex: theme.zIndex.mobileStepper + 1, // to go over popover
    [theme.breakpoints.down('sm')]: {
        display: 'none'
    }
}

const paperStyle = {
    mt: 4,
    ml: 4,
    p: 3,
    width: theme.spacing(36),
    height: theme.spacing(36),
    borderRadius: theme.shape.borderRadius * 7,
    pointerEvents: 'all'
};